/*矩阵数组相关工具*/
const matrixToolkit = {
	makeRow(v = 0) {
		const array = new Array(9);
		array.fill(v);
		return array;
	},

	makeMatrix(v = 0) {
		return Array.from({ length: 9 }, () => this.makeRow(v));
	},

	shuffle(array) {
		const endIndex = array.length - 2;
		for(let i = 0; i <= endIndex; i++) {
			const j = Math.floor(Math.random() * (array.length - i));
			[array[i], array[j]] = [array[j], array[i]];
		}
		return array;
	},

	checkFillable() {
		return true;
	}
};

